import Reac, { useState, useEffect } from 'react';
import { Routes, Route } from "react-router-dom"

import logo from './logo.svg';
import './App.css';
import BluetoothTerminal from './Helpers/BluetoothTerminal';
import VariantButtonGroup from './ModeSelectButtons';
import { Button } from '@mui/material';
import BluetoothConnectedIcon from '@mui/icons-material/BluetoothConnected';
import ResponsiveAppBar from './ResponsiveAppBar';
import SimpleBottomNavigation from './BottomNavBar';
import ColorPickerPage from './ColourPickerPage';



const ledSettings = {NumLEDS:-1};
var serviveUuid = "6e400001-b5a3-f393-e0a9-e50e24dcca9e";
var characteristicUuid  = "6e400003-b5a3-f393-e0a9-e50e24dcca9e";

const terminal = new BluetoothTerminal(serviveUuid,characteristicUuid,'\n','\n');

// Create the event.



 terminal.receive = function(data) {
  let option = data.charAt(0);
  if(option == "S")
  {
      let options = data.split(":");

      ledSettings.NumLEDS = parseInt(options[1]);
      ledSettings.Brightness = parseInt(options[2]);
      ledSettings.CurrentAnimationMode = parseInt(options[3]);
      const customEvent = new Event("SettingsUpdated", {"bubbles":true, "cancelable":false});
      document.dispatchEvent(customEvent);



  }
};


function App() {


  const [espsettings, setEspSettings] = useState(ledSettings);
  
  const handleSettingsUpdated = (event) => {
    setEspSettings({...ledSettings});

  };

  useEffect(() => {
    window.addEventListener("SettingsUpdated", handleSettingsUpdated);

    return () => {
      window.removeEventListener('SettingsUpdated',handleSettingsUpdated);
    };
  }, []);



  return (
    <div className="App">
      <ResponsiveAppBar setts={espsettings} />
      <section>
        <Button startIcon={<BluetoothConnectedIcon />}  onClick={() => terminal.connect().then(()=>terminal.send("S"))}>BT Connect</Button>
      </section>

      <Routes>
        <Route path="/" element={<VariantButtonGroup ledsettings={espsettings} terminal={terminal} /> } />
        <Route path="/colorpicker" element={<ColorPickerPage ledsettings={espsettings} terminal={terminal} /> } />

      </Routes>
      <SimpleBottomNavigation />
    </div>
  );
}

export default App;
