import * as React from 'react';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';


export default function VariantButtonGroup({terminal,ledsettings}) {


  const modes = [
    { title: 'Off', id: 0 },
    { title: 'FFT',  id: 1 },
    { title: 'AllHSV', id: 2 },
    { title: 'Fire2012WithPalette', id: 3 },
    { title: 'AllWhite', id: 4 },
    { title: 'BounceLED', id: 5 },
    { title: 'RainbowLed', id: 6 },
    { title: 'JunglePal', id: 7 },
    { title: 'Noise16_1', id: 8 },
    { title: 'Pride', id: 9 },
    { title: 'BackFowrwardWithPause', id: 10 }

  ];

  const listItems = modes.map(product =>
    <Grid item xs={6}>
   
    <Button  variant="contained"  sx={{fontSize: 12,  width: 1}}
      onClick={()=>{ terminal.send("M"+product.id);}}
    >
      {product.title}
    </Button>
   
    </Grid>
  );


  return (
    <Box justifyContent="center">
      <Grid sx={{padding:5}} container rowSpacing={1} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
        {listItems}
      </Grid>
    </Box>



  );
}