import Reac, {useEffect, useState } from 'react';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import { HexColorPicker } from "react-colorful";


export default function ColorPickerPage({terminal,ledsettings}) {

    const [color, setColor] = useState("#b32aa9");

    const [allLedColour, setAllLedColour] = useState("#b32aa9");

    const [sending,setSending] = useState(false);

    const [lastSkipped,setLAstSkipped] = useState(0);



    const updateAllColours = (val) => {
        setColor(val);
        setAllColourFunc(val);
    };

    
    const setAllColourFunc = (cc) =>
    {
        if(sending)
        {
            return;
        }
        setSending(true); 
        if(ledsettings.NumLEDS > 0)
        {
            let ledstring = "";
            for(let i =0; i < ledsettings.NumLEDS; i++)
            {      
                ledstring += cc;
            }
            console.log(ledstring);
            terminal.send("C" + ledstring).then(()=>{
                setSending(false);

            
            });
        }
    } 

  return (
    <section className="resposive example">
    <section>Number of LEDS: {ledsettings.NumLEDS}</section>

    <HexColorPicker color={color} onChange={e=> {updateAllColours(e); }} />

    <div className="value" style={{ borderLeftColor: color }}>
      Current color is {color}
    </div>


    </section>



  );
}