import * as React from 'react';
import Box from '@mui/material/Box';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import RestoreIcon from '@mui/icons-material/Restore';
import FavoriteIcon from '@mui/icons-material/Favorite';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import Paper from '@mui/material/Paper';
import { Link } from 'react-router-dom';

export default function SimpleBottomNavigation() {
  const pathname = window.location.pathname; // in case user visits the path directly. The BottomNavBar is able to follow suit.
  const [value, setValue] = React.useState(pathname);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (
    <Paper sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }} elevation={3}>

      <BottomNavigation
        showLabels
        value={value}
        onChange={handleChange}
      >
        <BottomNavigationAction component={Link} to="/" label="Animations" icon={<RestoreIcon />} />
        <BottomNavigationAction component={Link} to="/colorpicker"  label="Manual" icon={<FavoriteIcon />} />
        <BottomNavigationAction component={Link}  label="Settings" icon={<LocationOnIcon />} />
      </BottomNavigation>
 
    </Paper>

  );
}